<template>
  <div class="min-h-screen bg-gray-100 py-8">
    <div class="container mx-auto p-8 bg-white rounded-xl shadow-xl">
      <h1 class="text-3xl font-bold text-gray-800 mb-8 border-b pb-2">
        Edit Product
      </h1>

      <div v-if="product">
        <form @submit.prevent="updateProduct" class="space-y-6">

          <!-- Row 1: Name, Code, Category -->
          <div class="flex flex-wrap gap-6">
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Name</label>
              <input type="text" v-model="product.name" required
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Code</label>
              <input type="text" v-model="product.code" required
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Category UUID</label>
              <input type="text" v-model="product.category_uuid" required
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
          </div>

          <!-- Row 2: Serial No, Unit, Batch -->
          <div class="flex flex-wrap gap-6">
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Serial No</label>
              <input type="text" v-model="product.serial_no"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Unit</label>
              <input type="text" v-model="product.unit"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Batch</label>
              <input type="text" v-model="product.batch"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
          </div>

          <!-- Row 3: Purchase, Sales, MRP -->
          <div class="flex flex-wrap gap-6">
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Purchase Price</label>
              <input type="number" v-model="product.purchase_price"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Sales Price</label>
              <input type="number" v-model="product.sales_price"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">MRP</label>
              <input type="number" v-model="product.mrp"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
          </div>

          <!-- Row 4: Initial Stock, Size, MFG/EXP Dates -->
          <div class="flex flex-wrap gap-6">
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Initial Stock</label>
              <input type="number" v-model="product.initial_stock"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Size</label>
              <input type="text" v-model="product.size"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
            <div class="flex-1 min-w-[200px] flex gap-4">
              <div class="flex-1">
                <label class="block mb-2 font-semibold text-gray-700">MFG Date</label>
                <input type="date" v-model="product.mfg_date"
                  class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
              </div>
              <div class="flex-1">
                <label class="block mb-2 font-semibold text-gray-700">EXP Date</label>
                <input type="date" v-model="product.exp_date"
                  class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
              </div>
            </div>
          </div>

          <!-- Row 5: Asset -->
          <div class="flex flex-wrap gap-6">
            <div class="flex-1 min-w-[200px]">
              <label class="block mb-2 font-semibold text-gray-700">Asset</label>
              <input type="text" v-model="product.asset"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400" />
            </div>
          </div>

          <!-- Row 6: Description + Image -->
          <div class="flex flex-wrap gap-6">
            <div class="flex-1 min-w-[300px]">
              <label class="block mb-2 font-semibold text-gray-700">Description</label>
              <textarea v-model="product.description" rows="6"
                class="w-full border border-gray-300 px-4 py-2 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400"></textarea>
            </div>

            <div class="flex-1 min-w-[300px]">
              <label class="block mb-2 font-semibold text-gray-700">Image</label>
              <div class="border-2 border-dashed border-gray-300 rounded-lg p-12 text-center cursor-pointer hover:border-gray-400 transition-colors"
                   @click="triggerFileUpload" @dragover.prevent="handleDragOver"
                   @dragleave="handleDragLeave" @drop.prevent="handleDrop"
                   :class="{ 'border-blue-400 bg-blue-50': isDragging }">

                <div v-if="!imagePreview" class="text-gray-400">
                  <svg class="mx-auto h-12 w-12 mb-2" stroke="currentColor" fill="none" viewBox="0 0 48 48">
                    <path d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                          stroke-width="2" stroke-linecap="round" stroke-linejoin="round" />
                  </svg>
                  <p class="text-sm text-gray-500">Click to upload or drag and drop</p>
                  <p class="text-xs text-gray-400 mt-1">PNG, JPG, GIF up to 10MB</p>
                </div>

                <div v-else class="space-y-2">
                  <img :src="imagePreview || product.image" alt="Preview" class="max-w-full h-32 object-cover rounded-md mx-auto" />
                  <button type="button" @click.stop="removeImage" class="text-sm text-red-600 hover:text-red-700">
                    Remove Image
                  </button>
                </div>

                <input ref="fileInput" type="file" class="hidden" accept="image/*" @change="handleImageUpload" />
              </div>
            </div>
          </div>

          <!-- Row 7: Active checkbox -->
          <div class="flex items-center gap-4">
            <label class="inline-flex items-center gap-3 font-semibold text-gray-700">
              <input type="checkbox" v-model="product.is_active" class="form-checkbox h-5 w-5 text-blue-600" />
              Active
            </label>
          </div>

          <!-- Row 8: Buttons -->
          <div class="flex justify-end gap-4 mt-4">
            <button type="button" @click="$router.push('/productList')"
              class="bg-gray-500 text-white px-6 py-2 rounded-lg hover:bg-gray-600 shadow">
              Cancel
            </button>
            <button type="submit"
              class="bg-green-600 text-white px-6 py-2 rounded-lg hover:bg-green-700 shadow">
              Update Product
            </button>
          </div>

        </form>
      </div>

      <div v-else class="text-center text-gray-500 mt-6">
        Product not found!
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      product: null,
      imagePreview: null,
      isDragging: false,
    };
  },
  created() {
    const uuid = this.$route.params.uuid;
    const storedProducts = localStorage.getItem("products");
    if (storedProducts) {
      const products = JSON.parse(storedProducts);
      this.product = products.find((p) => p.uuid === uuid) || null;
    }
  },
  methods: {
    updateProduct() {
      const storedProducts = JSON.parse(localStorage.getItem("products")) || [];
      const index = storedProducts.findIndex((p) => p.uuid === this.product.uuid);
      if (index !== -1) {
        storedProducts[index] = this.product;
        localStorage.setItem("products", JSON.stringify(storedProducts));
        alert("Product updated successfully!");
        this.$router.push("/productList");
      } else {
        alert("Product not found!");
      }
    },
    triggerFileUpload() {
      this.$refs.fileInput.click();
    },
    handleImageUpload(e) {
      const file = e.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (event) => {
          this.imagePreview = event.target.result;
          this.product.image = event.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    removeImage() {
      this.imagePreview = null;
      this.product.image = null;
      this.$refs.fileInput.value = null;
    },
    handleDragOver() {
      this.isDragging = true;
    },
    handleDragLeave() {
      this.isDragging = false;
    },
    handleDrop(e) {
      this.isDragging = false;
      const file = e.dataTransfer.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (event) => {
          this.imagePreview = event.target.result;
          this.product.image = event.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
  },
};
</script>
